@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
body {
  margin: 0;
  font-family: 'Poppins';
  font-weight: 250;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(255, 255, 255);
  color: rgb(211, 211, 211)
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.home-div {
    width: 780px;
    margin: auto;
    margin-top: 25vh;
    
}
.inside-div {
    background-color: #eeeeee;
    padding: 30px;
    margin: auto;
}
.inside-div .libraryInput {
    position: relative;
    left: 240px;    
    margin: auto;
    text-align: center;
}
.home-div h1 {
    color: black;
    margin: auto;
    text-align: center;
}
.home-div h2 {
    margin:auto;
    text-align: center;
}
.home-div p {
    font-size: 13px;
    text-align: center;
    color: black;
}
.home-div form {
    text-align: center;
    margin: auto;
    width:300px;
    height:150px;
    background-color: #e2e1e1;
    padding: 20px;
}
.home-div form label {
    background-color: #ffffff;
    color: #000000;
    padding: 0.5rem;
    border-radius: 0.3rem;
    cursor: pointer;
    margin-top: 1rem;
    transition:cubic-bezier(0.075, 0.82, 0.165, 1) 1s;

}

.home-div form label:hover {
    color: black;
    background-color: #eeeeee;
}

.home-div form #file-chosen {
    position: relative;
    top: 15px;
    font-size: 12px;
    color: black;
}

.home-div form #submit {
    cursor: pointer;
    color: #000000;
    font-weight:250;
    background-color: #ffffff;
    border: 0px;
    border-radius: 0.3rem;
    padding:10px;
    margin-top: 3rem;
    transition:cubic-bezier(0.075, 0.82, 0.165, 1) 1s;
}
.home-div form #submit:hover {
    
    color: black;
    background-color: #eeeeee;
}
